import { trigger, style, query, group, transition, animate } from '@angular/animations';

function cubicBezierAnimation(time = '.6s', x1 = '.35', y1 = '0', x2 = '.25', y2 = '1') {
  return `${time} cubic-bezier(${x1}, ${y1}, ${x2}, ${y1})`;
}

function pageTransition(start: number, end: number) {
  const DEFAULT_POSITION = {
    position: 'absolute',
    top: '84px',
    left: 0,
    right: 0
  };

  const EXTREMELY_RIGHT = 'translateX(100%)';
  const EXTREMELY_LEFT = 'translateX(-100%)';
  const CENTER = 'translateX(0)';
  if (!start || !end) {
    throw new Error('Start and End page have to be provided');
  }

  if (start === end) {
    throw new Error('Transition to the same page is not allowed');
  }

  let startPosition = EXTREMELY_LEFT;
  let endPosition = EXTREMELY_RIGHT;

  if (start < end) {
    startPosition = EXTREMELY_RIGHT;
    endPosition = EXTREMELY_LEFT;
  }

  return transition(`${start} => ${end}`, [
    style({ height: '!' }),
    query(':enter', style({ transform: startPosition })),
    query(':enter, :leave', style(DEFAULT_POSITION)),
    group([
      query(':leave', animate(cubicBezierAnimation(), style({
        transform: endPosition
      }))),

      query(':enter', animate(cubicBezierAnimation(), style({
        transform: CENTER
      })))
    ])
  ]);
}

export const routeAnimation = trigger('routeAnimation', [
  pageTransition(1, 2),
  pageTransition(1, 3),
  pageTransition(2, 3),
  pageTransition(2, 1),
  pageTransition(3, 2),
  pageTransition(3, 1)
]);



