import { Component, AfterViewInit, Input, ViewContainerRef, Output, EventEmitter, OnInit, HostListener } from '@angular/core';
import { fadeAnimation } from '../shared/fade.animation';

@Component({
  selector: 'app-modal',
  templateUrl: './modal.component.html',
  styleUrls: ['./modal.component.css'],
  animations: [fadeAnimation]
})
export class ModalComponent implements OnInit, AfterViewInit {

  @Input() photo: string;
  @Input() photo_position: string;
  @Input() title: string;
  @Input() when: string;
  @Input() where: string;
  @Input() full_title: string;
  @Input() story;
  @Input() modalState;

  constructor(
    private viewContainerRef: ViewContainerRef
  ) { }

  ngOnInit() {
    setTimeout(() => {
      this.modalState = {
        value: 'active',
        params: { duration: '250ms' }
      };
    });
  }

  @HostListener('window:keyup', ['$event'])
  keyEvent($event: KeyboardEvent) {
    if ($event.keyCode === 27) {
      this.close();
    }
  }

  ngAfterViewInit() {
  }

  close() {}

  onFadeDone($event) {}

}



