import { AnimationBuilder, AnimationPlayer, animate, style } from '@angular/animations';
import { Component, ViewChild, Input } from '@angular/core';

@Component({
  selector: 'app-loading',
  template: `
 <div class="loading-stage">
   <div class="loading-bar" #loadingBar>
     {{ percentage * 100 }}%
   </div>
 </div>
<button (click)="play()">play</button>
<button (click)="pause()">pause</button>
<input type="range" min="0" max="1" step="0.1" [(ngModel)]="percentage">
  `,
  styles: [`
.loading-stage {
  border: 1px solid #444;
}
.loading-bar {
  background-color: red;
}
  `]
})
export class LoadingComponent {

  @ViewChild('loadingBar')
  public loadingBar;

  public player: AnimationPlayer;
  private _percentage = 0;

  constructor(private _builder: AnimationBuilder) {}

  get percentage() { return this._percentage; }

  @Input('percentage')
  set percentage(p: number) {
    this._percentage = p;

    if (this.player) {
      this.player.destroy();
    }

    const factory = this._builder.build([
      style({ width: '0' }),
      animate('5s cubic-bezier(.35,0,.25,1)', style({ width: (p * 100) + '%' }))
    ]);
    this.player = factory.create(this.loadingBar.nativeElement, {});
    this.player.play();
  }


  play() {
    this.player.play();
  }

  pause() {
    this.player.pause();
  }

}



