import { Component, Input, ElementRef, ComponentFactoryResolver, ViewContainerRef } from '@angular/core';
import { AnimationPlayer, NoopAnimationPlayer, AnimationBuilder, query, style, stagger, animate } from '@angular/animations';

import { appearChildAnimation, appearAnimation } from '../shared';
import { ModalComponent } from '../modal/modal.component';

@Component({
  selector: 'app-stories',
  templateUrl: './stories.component.html',
  styleUrls: ['./stories.component.scss'],
  animations: [appearChildAnimation, appearAnimation]
})
export class StoriesComponent {

  public done = false;
  public paused = false;

  @Input('position')
  set position(pos: number) {
    this.pause();
    this.player.setPosition(pos / 100);
  }

  private _stories;
  get stories() { return this._stories; }

  @Input('stories')
  set stories(stories) {
    this._stories = stories;

    Promise.resolve(true).then(() => {
      this.start();
      this.play();
    });
  }

  @Input() public state;

  public player: AnimationPlayer = new NoopAnimationPlayer();

  constructor(
    private _builder: AnimationBuilder,
    private _element: ElementRef,
    private componentFactoryResolver: ComponentFactoryResolver,
    private viewContainerRef: ViewContainerRef,
  ) {}

  pause() {
    this.paused = true;
    this.player.pause();
  }

  play() {
    this.paused = false;
    this.player.play();
  }

  start() {
    this.player = this._buildPlayer();

    this.player.onStart(() => {
      this.done = false;
    });

    this.player.onDone(() => {
      this.done = true;
    });
  }


  private _buildPlayer() {
    const animation = this._buildAnimation();
    return animation.create(this._element.nativeElement);
  }

  private _buildAnimation() {
    return this._builder.build([
      query('.cn-animation-container .cn-animation-item', [
        style({ opacity: 0, transform: 'translateY(-50px)' }),
        stagger(100, [
          animate('500ms', style({ opacity: 1, transform: 'none' }))
        ])
      ])
    ]);
  }

  openStory(story) {
    const componentFactory = this.componentFactoryResolver.resolveComponentFactory(ModalComponent);
    const componentRef = this.viewContainerRef.createComponent(componentFactory);
    Object.assign(componentRef.instance, story);
    document.body.style.overflowY = 'hidden';

    componentRef.instance.close = () => {
      componentRef.instance.modalState = 'inactive';
    };

    componentRef.instance.onFadeDone = ($event) => {
      if ($event.fromState === 'active' && $event.toState === 'inactive') {
        document.body.style.overflowY = 'auto';
        this.viewContainerRef.clear();
      }
    };
  }


}



