import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';

import { AppearDirective } from './appear.directive';
import { LoadingComponent } from './loading.component';

@NgModule({
  imports: [FormsModule],
  declarations: [
    AppearDirective,
    LoadingComponent
  ],
  exports: [
    FormsModule,
    AppearDirective,
    LoadingComponent
  ]
})
export class SharedModule {}



