import { Component, ViewChild, AfterViewInit } from '@angular/core';

import { Observable } from 'rxjs/Observable';
import { Subscription } from 'rxjs/Subscription';

import { revealAnimation, appearAnimation, appearChildAnimation } from '../shared';


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  animations: [
    revealAnimation,
    appearAnimation,
    appearChildAnimation
  ]
})
export class HomeComponent implements AfterViewInit {

  appearedState = 'hidden';
  @ViewChild('coverSection') coverSection;
  @ViewChild('aboutMeSection') aboutMeSection;
  @ViewChild('whatIDoSection') whatIDoSection;
  @ViewChild('methodologySection') methodologySection;
  sections: any[];
  lastScrollPosition = 0;
  loadingPercentage = 0;

  scroll$: Observable<any>;
  scrollSubscription;
  aboutMeState = 'inactive';

  ngAfterViewInit() {
    this.scroll$ = Observable.fromEvent(window, 'scroll')
      .distinctUntilChanged()
      .debounceTime(250)
      .throttleTime(250);

    this.setLastScrollPosition();

    const sections = [this.coverSection, this.aboutMeSection, this.whatIDoSection, this.methodologySection];
    this.sections = sections.map(section => {
      return {
        start:  section.nativeElement.offsetTop,
          end:  section.nativeElement.offsetTop + section.nativeElement.offsetHeight
      };
    });
  }

  onAppear() {
    setTimeout(() => {
      this.appearedState = 'visible';
    });
  }

  goToAboutMe($event) {
    $event.preventDefault();
    window.scroll({
      top: this.aboutMeSection.nativeElement.offsetTop - 84,
      behavior: 'smooth'
    });
  }

  private scrollToNextSection($event) {
    $event.preventDefault();

    const position = window.scrollY + 84;
    const direction = (this.lastScrollPosition < position) ? 1 : -1;
    const top = direction > 0 ? 'end' : 'start';
    const currentSection = this.sections
      .find(section => (position >= section.start && position <= section.end));

    this.setLastScrollPosition();

    window.scroll({
      top: currentSection[top] - 84 + (top === 'end' ? 0 : 1),
      behavior: 'smooth'
    });
  }

  private setLastScrollPosition() {
    this.lastScrollPosition = window.scrollY + 84;
  }


}



