import { trigger, transition, query, style, state, stagger, animate } from '@angular/animations';

export const appearAnimation = trigger('appear', [
  transition('hidden => visible', [
    query('.cn-animation-container .cn-animation-item', style({ transform: 'translateY(-50px)', opacity: 0 })),
    query('.cn-animation-container', [
      stagger(500, [
        query('.cn-animation-item', [
          stagger(300, [
            animate('800ms cubic-bezier(.35,0,.25,1)', style({ transform: 'translateY(0)', opacity: 1 }))
          ])
        ])
      ])
    ])
  ])
]);

export const appearChildAnimation = trigger('child', [
  state('hidden', style({ transform: 'translateY(-50px)', opacity: 0 }))
]);



